.side-bar{
    $n: &;
    z-index: 111;
    position: absolute;
    height: 100vh;
    width: 20vw;
    background-color: white;
    right: 0;
    top: 0;
    justify-items: center;
    display: none;    
    grid-template-rows: 4em 2em;
    
    p{
        margin: 0;
    }
    &.display{
        display: grid;   
    }
    .hl{
        border-bottom: 0.2px solid $light-gray;
        height: 10pt;
        width: 100%;
        opacity: 35%;
    }

    #{$n}__title{
        font-weight: bold;
        font-size: $body-text;
        color: $theme-color;
        margin: auto;
    }    
    #{$n}__link{
        display: block;
        // margin: auto;
        font-size: 1.2em;
        font-weight: bold;
        text-align: center;
        color: $dark;
        width: 100%;
        padding-top: 20%;

        &.active{
            z-index: 118;
            background-color: $theme-color;
            color:white;
            width: 100%;
            right: 0;
            svg g path{
                fill: white;
            }
        }

        p{
            margin: 0.5em auto;
        }

        .icon{
            padding: 0 2em;
        }
    }   
}


    .dimSidebar{
        background-color: rgb(216, 224, 234);
        z-index: 111;
        position: absolute;
        height: 100vh;
        width: 20vw;
        top: 0;
        opacity: 80%;
        display: none;
        &.active{
            display: block;
        }
    }