.education-step {
    position: relative;
    background-color: #eee;
    padding: 2.8em;
    border-radius: 8px;
    margin: auto;
    width: 100%;
    max-width: 61em;

    display: grid;
    grid-template-columns: 5em 5em 1fr;
    grid-template-rows: 1fr auto;
    grid-gap: 2em;
    transition: all .5s ease-in-out;

    &__arrows {
        position: relative;

        span:first-child{
            margin-right: .75em;
        }
    }



    &__step {
        color: #0386D6;
        font-size: 1.2em;
        font-weight: bold;
    }

    &__instruction {
        font-size: 1.4em;
        color: rgba(90,102,117,0.6);
    }

    &__guide {
        grid-column: 3 / -1;

        display: flex;
        align-items: center;

        label {
            font-weight: bold;
            margin-left: 1em;
            font-size: 1.2em;
            color: rgb(47,59,74);

            &.active {
                color: #0386D6;
            }
        }
    }

    &--inverted {
        background-color: rgb(47,59,74);

        .education-step{
            &__instruction {
                color: #fff;
            }

            &__guide {
                label {
                    color: #fff;
                }
            }            
        }

        .aarrow{
            &:not(.aarrow--disabled)
            {
                &::before{
                    border-color: #fff !important;
                }
            }

            &--disabled
            {
                &::before{
                    border-color: #6b6b6b !important;
                }
            }
        }

    }
}

.circle {
    background: #0386D6;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 1.7em;
    height: 1.7em;

    .aarrow::before {
        width: .4em;
        height: .4em;
    }
}

.aarrow{
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 1.5em;
    height: 1.5em;
    border: none;
    
    &::before {
        content: '';
        width: .5em;
        height: .5em;
        border: 1px solid #0386D6;
        border-bottom: none;
        border-right: none;
        transform: rotate(-45deg);
    }
    
    &--left{
        &::before {
            transform: rotate(-45deg);
        }
    }

    &--right{
        &::before {
            transform: rotate(135deg);
        }
    }

    &--disabled {
        &::before {
            border-color: #b7b7b7;
        }
    }

    &--white {
        &::before {
            border-color: #fff;
        }
    }
}