.practice-id{
    display: flex;
    height: 85vh;
    width: 100vw;

    &__hero{
        width: 50%;
        background-image: url('../../assets/images/calque1353@3x.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
    }

    &__forms{
        width: 50%;
        padding: 1em 4em;

        .pid-btn{
            position: absolute;
            right: 0;
            margin-right: 5em;
            margin-top: 2em;
        }
    }
}