.my-companion{
    color: $theme-color;
    height: 83vh;
    width: 100vw;
    position: absolute;
    bottom: 0;

    &__center{        
        display: flex;
        padding-right: 1em;
    }
    &__left{
        display: block;        
        margin: 1em;
        .btn-group{
            border: 0.08em solid $border-light-blue;
            border-bottom: unset;
            border-radius: 0.75em;
            

            .companion-btn{
                border-radius: unset;
                border: unset;                
                border-bottom: 0.08em solid $border-light-blue;    
            }
            .companion-btn:first-child{
                border-top-left-radius:  0.75em;
                border-top-right-radius:  0.75em;
            }
            .companion-btn:last-child{
                border-bottom-left-radius:  0.75em;
                border-bottom-right-radius:  0.75em;
            }
        }
    }
    &__right{
        width: 100%;
        max-height: 65vh;
        overflow-y: scroll;

        .price-view{
                position: absolute;
                right: 20%;
                border: 1px solid $theme-color;
                border-radius: 1.39em;
                color: $theme-color;
                padding: 0.25em 0.81em;
                width: 4em;
                text-align: center;
                &.sub{
                    right: 0;
                }
        }
    }
    &__footer{
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 5%;
        width: inherit;
        .first{
            display: flex;
        }
        .next-btn{
            border: 1px solid $theme-color;
            svg {
                width: 14pt;
                height: 100%;
                padding-left: 1em;
                g path{ 
                    fill: white;
                }
            }
            h2.text{
                padding: 0;
            }
            &.recommended{
                    width: 22.5em;
                    background-color: white;
                    
                    h2.text{
                        color: $theme-color;
                        font-weight: normal;
                    }
                    svg g path{
                        fill: $theme-color;
                    }
            }
        }
        .price-btn{
            padding: 1em 0.8em;
            justify-content: center;
            margin: 0 0.5em;
        }
    }
    .hl{
        border-bottom: 0.2px solid $light-gray;
        height: 10pt;
        width: 100%;
        opacity: 35%;
        margin-top: 1em;
        margin-bottom: 2em;
        &.footer{
            margin: 0;
            position: absolute;
            bottom: 18%;
        }
    }
}