.education-landing{
    height: 100%;
    padding: 0 50px;
    box-sizing: border-box;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;

    &__instruction {
        text-align: center;
        margin-bottom: 25px;

        &-head {
            margin: 0;
            font-size: 26px;
            color: $gray;
        }

        &-text{
            color:rgba(90,102,117, 0.6);
            font-size: 14px;
            width: 600px;
        }
    }

    &__card-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;       
    }

    &__card {
        width: 31%;
        height:400px;
        box-shadow: 0px 0px 20px 0px #3333339e;
        overflow: hidden;
        border-radius: 10px;
        position: relative;

        &-background {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;

            img {
                width: 100%;
            }

            .cover {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 45%;
                overflow: hidden;
                backdrop-filter: blur(7px);
                
                .info{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: rgba($gray, .5);

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-flow: column;
                    color: #fff;

                    span{
                        filter: invert(1);
                        img {
                            filter: brightness(0);
                        }
                    }

                    label {
                        font-size: 19px;
                        margin: 15px 0 0;
                    }
                }


                &--soon {
                    height: 100%;

                    img{
                        transform: translateY(0);
                    }

                    .info {
                        span {
                            display: none;
                        }
                        label{
                            font-size: 25px;
                            margin: 0 0 20px;
                        }
                        button{
                            background: #fff;
                            border: none;
                            padding: 7px 20px;
                            border-radius: 19px;
                            font-size: 15px;
                            font-weight: bold;
                            color: $theme-color;
                        }
                    }
                }
            }
        }
    }
}