%main-page{
    height: 85vh;
    width: 100vw;
    display: flex;
    position: absolute;
    bottom: 0;
}

.measures-page{
    @extend %main-page;

    &__step1{
        @extend %main-page;
        background-image: url('../../assets/images/measures01.png');
        background-size: 100% auto;
    }

    &__step2{
        @extend %main-page;
        background-image: url('../../assets/images/measures02.png');
        background-size: 100% auto;
    }

    &__step3{
        @extend %main-page;
        background-image: url('../../assets/images/measures03.png');
        background-size: 100% auto;
    }

    &__progress{
        width: 12em;
        height: 100%;
        z-index: 100;
        list-style-type: none;
        list-style-type: none;
        padding-left: 0;
        margin-top: 0;

        &--step.active{
            height: calc(100%/3);
            border: 5px solid $theme-color;
            border-top: none;
            border-right: none;
            border-bottom: none;
            position: relative;
            display: table;

            .content{
                display: table-cell; 
                vertical-align: middle; 
                text-align: left;
                padding-left: 1em;
                color: $theme-color;

                h5{
                    color: $theme-color;
                    margin-bottom: 0.5em;
                    
                }

                h3{
                    font-weight: 100;
                    margin-top: 0;
                    font-size: 1.4em;
                }
            }
        }

        &--step{
            height: calc(100%/3);
            border: none;

            .content{
                height: calc(100%/3);
                border-top: none;
                border-right: none;
                border-bottom: none;
                position: relative;
                display: table;

                h4{
                    display: table-cell; 
                    vertical-align: middle; 
                    text-align: left;
                    padding-left: 1em;
                    color: $theme-color;
                }
            }
        }
    }

    &__modal{
        width: 26em;
        right: 0;
        height: -webkit-fill-available;
        position: absolute;

        &--box{
            width: auto;
            margin: 2em;
            background-color: #ffffff;
            height: auto;
            border-radius: 1em;
            padding: 1.5em;

            .mp-header{
                display: flex;

                p{
                    padding: 0;
                    margin: 0.7em 0em 0em 0em;
                    font-weight: 900;
                    font-size: 11pt;
                    color: $dark;
                }

                button{
                    margin-left: auto;
                }
            }

            .mp-content{
                .headercontent{
                    font-weight: 500;
                    color: $dark;
                }

                .subcontent{
                    color: $light-gray;
                }

                .textbox-group{
                    display: flex;

                    label{
                        color: $theme-color;
                        font-size: 11pt;
                        min-width: 7em;
                        margin-top: 0.5em;
                        font-weight: 600;
                        display: flex;

                        .rectangle{
                            display: flex;
                            border: 1px solid $theme-color;
                            width: 1em;
                            height: 0.5em;
                            margin: 7% 0;
                        }
                    }
                }

                .measures2-tbox{
                    display: flex;

                    div.left,
                    div.right{
                        display: block;
                        width: 50%;
    
                        label{
                            color: $theme-color;
                            font-size: 11pt;
                            min-width: 7em;
                            margin-top: 1.2em;
                            font-weight: 100;
                            margin-left: 5px;
                        }

                        label.left-label,
                        label.right-label{
                            font-weight: 600;
                            display: block;
                            margin: 0 0 1.2em 5px;
                        }

                        .mp-left-eye{
                            width: 65%;
                        }
                    }
                }


            }

            .mp-footer{
                button{
                    margin: 1em 0em 0em auto;
                }
            }
        }
    }

   .measures-modal-content{
       .measures-modal-content{
            text-align: -webkit-center;

           .btn-header{
                display: flex;
               .icon-btn{
                    margin-left: auto;
                    padding-right: 3em;
               }
           }

           h2.measures-modal-header{
            margin: 2em 2em 1.5em 2em;
           }

           .basic-icon-btn{
                margin-bottom: 2em;
           }

           .link-backToLens{
               color: $light-gray;
               text-decoration: underline;
               padding-top: 1em;
               font-size: 10pt;
           }
       }
   }

   .measures-import-modal-content{
         text-align: -webkit-left;

        .btn-header{
             display: flex;
            .icon-btn{
                 margin-left: auto;
                 padding-right: 3em;
            }
        }

        h2.measures-import-modal-header{
         margin: 2em 2em 1.5em 2em;
        }

        .panel-import-buttons{
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .basic-icon-btn{
             margin-bottom: 2em;
        }

        .link-backToLens{
            color: $light-gray;
            text-decoration: underline;
            padding-top: 1em;
            font-size: 10pt;
        }
    }
}