.lpb-container {
    background-image: url('../../assets/images/lpb-hero.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    width: 70%;
    margin: 1em;
    text-align: center;
    border-radius: 1.25em;
    position: relative;

    img.i-glasses {
        margin-top: 5em;
        width: 4em;
    }

    &__start {
        display: inline-flex;
        margin-top: 3em;
    }

    &__header {
        color: #ffffff;
        font-size: 2.5em;
        margin: 0em 4em;
    }

    &__panel {
        position: absolute;
        bottom: 0;
        width: -webkit-fill-available;
        display: flex;
        text-align: center;
        margin: 2em;
        padding: 1em;
        @extend %blur-container;
        border-radius: 1em;

        .panel-text {
            color: #ffffff;
            font-size: 1em;
            margin: auto 0;
            margin-left: 1em;
        }

        .panel-buttons {
            margin-left: auto;
            display: flex;
        }
    }

    &__category-panel {
        position: absolute;
        bottom: 0;
        width: -webkit-fill-available;
        display: block;
        text-align: center;
        margin: 2em;
        padding: 1.5em;
        @extend %blur-container;
        border-radius: 1em;

        .header {
            display: flex;
            margin-bottom: 2em;

            .question {
                color: #ffffff;
                font-size: 1.2em;
                margin: 0;
                padding: 0;
            }

            .close {
                margin: -0.5em 2.8em 0em auto;
            }
        }

        .choices {
            width: 100%;
            height: 8em;

            button {
                width: 31%;
                height: -webkit-fill-available;
            }
        }

        .choices a:first-child button {
            margin-left: 0;
        }

        .choices a:last-child button {
            margin-right: 0;
        }
    }

    .lpb-modal-content {
        width: 35%;
        height: 25%;

        .modal-content {
            text-align: -webkit-left;

            .btn-header {
                display: flex;

                .icon-btn {
                    margin-left: auto;
                    padding-right: 3em;
                }
            }

            h2.lpb-modal-header {
                margin: 2em 2em 1.5em 2em;
                text-align: left;
                &.invalid{
                    margin: 2em;
                    text-align: center;
                }
            }

            .text-btn-grp {
                display: flex;
                margin: 0 2em;
                .compact{
                    height: 3.5em;
                }

                .lpb-id-tbox {
                    width: 100%;
                    margin: 0;
                }
            }
        }
    }
    .continue-container{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 3em;

        .exit-btn{
            background-color: white;
            padding: 1.1em 1.8em;
            img{                
                transform: rotate(45deg);
                height: 14pt;
            }
        }
        .continue-btn{
            margin-top: 0;
            margin-left: 8px;
            z-index: 2;
        }
        .tbox{
            border-color: white;
            width: 30%;
            @extend %blur-container;
            color: white;
            font-size: 14pt;
            font-weight: bold;
            margin: 0 0.2em;
        }
    }
}