.packagebtn {
    font-size: $sub-text;
    border: 1pt solid rgba(0, 134, 214, 0.2);
    border-radius: 35.5pt;
    padding: 17pt 30pt;

    svg {
        float: right;
    }
}

.packagelist {
    .button__white {
        border: none;

        .content {
            color: $theme-color;
        }

        .center {
            padding: 5pt 20pt;
        }

        .content-icon {
            height: 35pt;
            width: 35pt;
            margin: 33px 0;

            .img {
                margin: 15px;

                svg {
                    width: 12pt;
                }
            }
        }
    }
    .package__answered{
        font-size: $body-text;
        padding: 5pt 0pt;
        flex-grow: 1;
        margin: 10pt;
        flex-basis: 40%;
        
        .package-title{
            margin-left: 30px;
            margin-bottom: 5pt;
            margin-top: 10pt;
            text-transform: capitalize;
        }
        &--container{
            background-color: rgba(0,134,214,.10);
            color: var(--gray);
            border-radius: 12pt;
            display: flex;
            padding-right: 20pt;
            .correct-product{
                font-size: $sub-text;
                flex: 1;                
                margin: 19pt 25pt;   
            }
            .content-icon{
                height: 25pt;
                width: 25pt;
                background-color: white;
                border-radius: 50%;
                display: inline-block;
                color: $theme-color;
                margin: 24px 5px;
                border: 1pt solid $theme-color;
                    svg{
                        margin: 0 10px;
                        width: 10pt ;
                        height: 100%;
                    }
            }
        }
    }

}

.vl {
    border-bottom: 1px solid rgba(90, 102, 117, 0.1);
}



.product {

    &__list {
        button {
            width: 95%;
            margin: 10pt 0pt;
            position: relative;
            justify-content: end;
        }

        &--card {
            position: relative;
            padding: 1% 0;
            font-size: $body-text;
            color: var(--gray);

            .border {
                width: 5pt;
                background-color: $theme-color;
                height: 100%;
                float: left;
                border-top-right-radius: 2.5pt;
                border-bottom-right-radius: 2.5pt;
                position: absolute;
                top: 0;
            }
            &.selected{
                opacity: 1;
                background-color: rgb(204, 231, 247);
                color: $theme-color;
                transition: 400ms;
            }
            &.sub{
                padding:0;
            }
            .display{
                display: none;
            }
        }

        &--content {
            margin: 11pt 21pt;
            font-size: $body-text;
            

            .arrow-down {
                float: right;
                margin: 5pt 0;
                height: 8pt;
                width: 10%;
            }

            .arrow-up {
                float: right;
                margin: 5pt 0;
                height: 8pt;
                width: 10%;
                -webkit-transform: scaleY(-1);
                transform: scaleY(-1);
            }
           
        }
    }
}

.product-btn{
    border: 1pt solid rgba(0, 134, 214, 0.2);
    flex-direction: row-reverse;
    background-color: white;    
    
    .text{
        padding-top: 0.5em !important;
        color: var(--gray) !important;
        font-weight: 300 !important;
        position: absolute;
        left: 10%;
    }
}