.prescription{
    display: flex;
    color: $theme-color;
    height: 85vh;
    width: 100vw;
    position: absolute;
    bottom: 0;

    &__left{
        width: 40%;

        .container{
            background-color: #ffffff;
            -webkit-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            -moz-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            border-radius: 15pt;
            width: -webkit-fill-available;
            height: 35em;
            margin: 1.5rem;

            .border {
                width: 5pt;
                background-color: $theme-color;
                height: 100%;
                float: left;
                border-top-right-radius: 25pt;
                border-bottom-right-radius: 25pt;
                -webkit-box-shadow: 5px 0px 11px 0px rgba(0, 134, 214, 0.2);
                -moz-box-shadow:5px 0px 11px 0px rgba(0, 134, 214, 0.2);
                box-shadow: 5px 0px 11px 0px rgba(0, 134, 214, 0.2);
            }

            .content{
                padding: 0.5em 2em;
                height: -webkit-fill-available;
                position: relative;

                &__header{

                    div{
                        margin: 1.3rem 1rem 0rem 0rem;
                    }

                    display: flex;
                }

                &__image img{
                    width: 18em;
                    padding: 3em 0em;
                }

                &__buttons{

                    .btn-grp{
                        display: flex;
                        position: absolute;
                        bottom: 5%;
                        right: 7%;
                    }
                }
            }
        }
    }

    &__right{
        width: 60%;
        padding: 2em;

        .navigator{
            justify-content: center;
            display: flex;
            width: auto;

            .container{
                text-align: center;
                display: flex;
                background-color: rgb(204, 231, 247);
                padding: 0.6em;
                width: 26em;
                border-radius: 1em;
                align-items: center;
                justify-content: center;

                &__btn{
                    padding: 0.6em;
                    border-radius: 0.6em;
                    width: 13em;
                    font-size: 1.1em;
                }

                &__btn.active{
                    background-color: #ffffff;
                    -webkit-box-shadow: 0px 0px 5px 1px rgba(3,134,214,0.15);
                    -moz-box-shadow: 0px 0px 5px 1px rgba(3,134,214,0.15);
                    box-shadow: 0px 0px 5px 1px rgba(3,134,214,0.15);
                }
            }
        }

        .sca{
            padding: 2em 0em;
        }

        .add{

            &__title{
                font-size: 0.8em;
                padding: 0em 0em 1em 2em;
                color: $theme-color;
            }

            &__tbox{
                width: auto;
                background-color: #ffffff;
                padding: .5em 0em;
                color: var(--gray);
                text-align: center;
                border-radius: 0.5em;
                border: 1px solid rgb(204, 231, 247);
                font-size: 1.2em;
            }
        }

        .lens-choices{
            .back-btn{
                margin: 0;
            }

            .scroll{
                overflow-x: auto;
                height: 33em;
            }
        }
    }

    .prescription-import-modal-content{
        text-align: -webkit-left;

       .btn-header{
            display: flex;
           .icon-btn{
                margin-left: auto;
                padding-right: 3em;
           }
       }

       h2.prescription-import-modal-header{
        margin: 2em 2em 1.5em 2em;
       }

       .panel-import-buttons{
           display: flex;
           align-items: center;
           justify-content: center;
       }

       .basic-icon-btn{
            margin-bottom: 2em;
       }

       .link-backToLens{
           color: $light-gray;
           text-decoration: underline;
           padding-top: 1em;
           font-size: 10pt;
       }
   }
}