.measures-summary {
    width: 100%;
    margin: 0 15%;

    

    hr {
        border-color: rgba(90, 102, 117, 0.1);
        width: 100%;
        margin: 1.5% 0;
    }

    &__header {
        display: flex;

        .title {
            font-size: 35pt;
            color: $dark;
            flex: 1;
            margin: 4% 0;
        }

        .basic-icon-btn {
            &.simple{
                height: 100%;
                margin: auto;
            }
        }
    }

    &-content {
        display: flex;
        margin-top: 4%;
        height: 62vh;
        hr {
            width: 88%;
        }
    }

    &__card {
        -webkit-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
        -moz-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
        box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
        border: none;
        width: 50%;
        max-height: 26em;
        border-radius: 8pt;
        position: relative;      

        .price-container {
            background-color: $theme-color;
            color: white;
            height: 16%;
            position: absolute;
            border-bottom-right-radius: 15pt;
            border-bottom-left-radius: 15pt;
            width: 100%;
            bottom: 0;
            font-size: $sub-text;
            display: flex;
            justify-content: center;
            align-items: center;
            left: 0;
            font-weight: bold;
            position: sticky;

            .modify-white-icon {
                width: 11pt;
                height: 100%;
                margin-left: 8%;
            }
        }
        &--header {
        display: flex;
        margin: 2% 6%;
        position: sticky;

        h2 {
            flex: 1;
        }

        .price {
            border: 1px solid $border-light-blue;
            color: $theme-color;
            font-weight: bold;
            font-size: $sub-text;
            border-radius: 8pt;
            height: 2em;
            width: 30%;
            margin: auto;
            text-align: center;
            vertical-align: middle;
            line-height: 2em;
        }
    }


    }

    .answer-holder {
        border-radius: 3pt;
        color: $theme-color;
        border: 1px solid $theme-color;
        text-align: center;
        vertical-align: middle;
        line-height: 1.6em;
        width: 6em;
        height: 1.6em;
    }

    .content-title {
        text-decoration: underline;
        font-weight: bold;
        font-size: $body-text;
    }

    

    &__content {        
        overflow-y: scroll;
        max-height: 63%;
        font-size: $small-text;        
        margin: 0 6%;
        margin-bottom: 4%;

        &--measure1 {

            .content {
                // display: flex;
                color: $theme-color;

                &__pairone {
                    flex: 1;
                    display: flex;

                    .rectangle {
                        border: 1px solid $theme-color;
                        width: 1em;
                        height: 0.5em;
                        margin: 12% 0;
                    }

                    .content-a {
                        display: flex;
                        margin: auto 0;
                    }
                }
            }

            .answer-holder {
                margin-right: 3%;
                margin-left: 5%;
            }
        }

        &--measure2 {

            .title {
                text-decoration: underline;
                font-weight: bold;
                font-size: $body-text;
            }

            .content {
                color: $theme-color;

                &__container {
                    display: flex;
                    p{
                        margin: 1.5% 0;
                    }
                }

                .answer-label {
                    margin-right: 5%;
                }

                .answer-holder {
                    width: 8.5em;
                }
            }
        }

    }
    &__blank{
        border: 1px solid $border-light-blue;
        border-radius: 12pt;
        width: 50%;
        max-height: 27em;
        vertical-align: middle;
        text-align: center;
        margin-left: 5%;
        .icon{
            height: 50pt;
                width: 50pt;
                background-color: white;
                border-radius: 50%;
                display: inline-block;
                color: $theme-color;
                margin: 11.5em 0;
                border: 1pt solid $theme-color;

                .img {
                    margin: 22px;
                    svg{
                        width: 17pt ;
                        height: 100%;
                    }
                }
        }
    }
}