.package {
    height: 75vh;
    width: 100vw;
    display: flex;
    position: absolute;
    bottom: 0;
    justify-content: center;
    margin: 0 auto;
    transition: transform .2s;

    &__content {
        font-size: $sub-text;
        border: 1px solid rgba(0, 134, 214, 0.2);
        border-radius: 12pt;
        text-align: center;
        padding: 48pt 55pt;
        margin: 65pt 18pt;

        &--demobtn {
            padding-top: 188%;
        }

        &:hover {
            -webkit-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            -moz-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            border: none;
            transition: transform .2s;
            -webkit-transform: scale(1.2);
            transform: scale(1.2);
        }

        &.selected {
            -webkit-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            -moz-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            border: none;
            transition: transform .2s;
            -webkit-transform: scale(1.2);
            transform: scale(1.2);
        }
    }
}

a {
    text-decoration: none;
    color: $theme-color;
}

