.dropdown {
    position: relative;
    height: 3.4em;
    display: flex;
    background: rgb(90,102,117);
    border-radius: 3px;

    &__value {        
        flex: 1 1;
        display: flex;
        align-items: center;
        padding: 1em;
        color: #fff;
        background: transparent;
        border: none;
        border-radius: 3px;

        &--with-data {
            background-color: #0386D6;
        }

    }

    &__arrow {
        width: 3.6em;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        border: none;
        border-left: 1px solid #303b4a;
        background: transparent;
    }

    &__options {
        list-style: none;
        position: absolute;
        top: 110%;
        left: 0;
        width: 100%;
        background: #fff;
        border-radius: 3px;
        z-index: 1;

        li {
            padding: 1em;
            border-bottom: 1px solid rgba(48,59,74,0.3);
            color: #0386D6
        }
    }
}

.arrow {
    border-width: 0px 1px 1px 0px;
    border-style: solid;
    border-color: #fff;
    border-image: initial;
    padding: 0.2em;
    transition: all .25s ease-in-out;
    
    &--down{
        transform: rotate(45deg) 
    }

    &--up{
        transform: rotate(225deg) 
    }
}