.product-table{
    &__title{
        color: $theme-color;    
        margin-top: 3em;
        font-weight: bold;
    }

    &__table{
        border: 1px solid $border-light-blue;
        border-radius: 1em;
        width: 100%;

        &--header{
            padding: 1em 2em;
            background-color: $border-light-blue;
            border-top-left-radius: 1em;
            border-top-right-radius: 1em;
            color: $dark;
        }

        &--content{
            display: flex;
            padding: 2em;

            .icon-text{
                padding: 0.3em 2em;
            }

            .icon-text .icon-subtitle{
                color: $dark;
            }

            .icon-text:nth-child(2){
                border-left: 1px solid $border-light-blue;
                border-right: 1px solid $border-light-blue;
            }
        }
    }

    &__table.firstTable{
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    &__table.midTable{
        border-radius: 0;
    }

    &__table.midTable .product-table__table--header{
        border-radius: 0;
    }

    &__table.lastTable{
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }

    &__table.lastTable .product-table__table--header{
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
}