.breadcrumbs{
    width: 100vw;
    height: 37px;
    display: flex;
    position: sticky;
    z-index: 100;
    background-color: white;

    svg{
        width: 15px;
        height: 15px;
        margin: 0.3em 1em;

        g g g path{
            fill: $inactive-color;
        }
    }

    svg.icon-active{
        g g g path{
            fill: $theme-color;
        }
    }

    svg.lessThanIcon{
        padding: 0;
        margin: 0.6em 0.5em;
        width: 6px;
        height: 6px;
    }

    &__crumb{
        display: flex;

        .crumb-text{
            padding: 0;
            margin: 0.5em 0.7em;
            color: $inactive-color;
        }

        .crumb-text.active{
            color: $theme-color;
        }
        .active-circle{
            background-color: $theme-color;
            border-radius: 50%;
            width: 4pt;
            height: 4pt;
            margin: auto;
        }
    }

    &__selector{
        color: $inactive-color;
        font-size: .2em;
        padding: 0;
        top: 8px;
        position: relative;
        top: -8px;
        left: 21px;
    }
}
