.education-content {
    flex: 1;
    position: relative;
    padding-left: 1em;
    padding: 2em 3em 2em 6em;
    transition: all .5s ease-in-out;
    


    &::before{        
        content: '';
        background: #fff;
        height: 100%;
        width: 20px;
        position: absolute;
        top: 0;
        left: -10px;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        transition: all .5s ease-in-out;
    }

    &--inverted {
        background: rgb(22,30,40);

        &::before {
            background: rgb(22,30,40);
        }
    }

    &__message {
        color:rgba(90,102,117, 0.4);
        font-size: 2.5em;
        font-weight: normal;
        width: 220px;
        height: 70px;
        display: block;
        text-align: center;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
    }
}