.text-box{
    width: 100%;
    border: 1px solid $border-light-blue;
    border-radius: 0.5em;
    padding: 0.7em 1em;
    margin: 0.5em 0.2em;    
    font-weight: bold;
    font-size: $body-text;
    color: $dark;
}

.text-box:focus{
    outline: none;
}

.text-box.border-blue{
    border: 1px solid $theme-color;
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-weight: bold;
    font-size: $body-text;
    color: $dark;
  }
  