.lt-right{
    color: $dark;
    .display{
        display: block;
    }
    .display-none{
        display: none;
    }
    h2{
        font-weight: normal;
        font-size: $sub-text;
        margin: 3% 0;
    }
    .button__white {
        height: 2.5em;
        padding: 15pt 35pt;
        .center{
            height: 100%;
            padding: 0;
        }
        .content-left{
            margin: auto 0;
            h2{
                margin: 0;
            }
        }
    }
    .button__white .content-icon {
        height: 25pt;
        width: 25pt;
        margin: auto 0;
        .img{
            margin: 8px;
            svg{
                width: 13pt;
                height: 100%;
            }
        }
    }
    .button__blue{
        height: 2.5em;
        .center{
            height: 100%;
        }
        .content-icon{
            margin: auto;
            height: 25pt;
            width: 25pt;
            .img{
                margin: 8px;
                svg{
                    width: 13pt;
                    height: 100%;
                }
            }
        }
        .content-left{
            display: flex;
            &-status{
                height: 1.5em;
                margin: auto 0;
                margin-left: 1.5em;
            }
            h2 {
                margin: auto 0;
            }
        }
        
    }
    &__report{
        border: 2pt solid $border-light-blue;
        border-radius: 15pt;
        margin: 5% 2.4%;

        &--card{
            font-size: $sub-text;
            display: flex;
            border-bottom: 1px solid $border-light-blue;
            .text{
                padding: 20pt 19pt;
                flex: 1;
            }
        }
        &--value{
            color: $theme-color;
            background-color: $border-light-blue;
            float: right;
            font-size: $body-text;
            padding: 20pt 36pt;
            border-top-right-radius: 12pt;
        }
        &--summary{
            &.tri-layout{
                display: flex;
            }
            .text{
                color: $light-gray;
                font-size: $body-text;                
                flex: 1;
                margin: 2.5% 0;
            }
            .next-btn{
                width: 44%;
            }
            .footer{
                padding: 5% 3%;
            }
        }
    }
    .lens-slider{
        position: relative;
        margin-top: 12%;
        .css-yvszuv-Slider{
            width: 80%;
            margin: 2% 0;
            background-color:#eee;
            height: 1pt;
        }
        svg{
            margin: 0 3%;
        }
        .css-1ldz52l-Slider{
            background-color: $theme-color;
        }
        .slider-index{
            position: absolute;
            top: 26%;
            width: 100%;
        }
        .vl{
            
            height: 7pt;
            position: absolute;
            top: 22%;
            border-left: 1px solid $theme-color;
            // opacity: 10%;
        }
    }
    .prescription-container{
        .navigator{
            justify-content: center;
            display: flex;
            width: auto;

            .container{
                text-align: center;
                display: flex;
                background-color: rgb(204, 231, 247);
                padding: 0.6em;
                width: 26em;
                border-radius: 1em;
                align-items: center;
                justify-content: center;

                &__btn{
                    padding: 0.6em;
                    border-radius: 0.6em;
                    width: 13em;
                    font-size: 1.1em;
                }

                &__btn.active{
                    background-color: #ffffff;
                    -webkit-box-shadow: 0px 0px 5px 1px rgba(3,134,214,0.15);
                    -moz-box-shadow: 0px 0px 5px 1px rgba(3,134,214,0.15);
                    box-shadow: 0px 0px 5px 1px rgba(3,134,214,0.15);
                }
            }
        }

        .sca{
            padding: 2em 0em;
        }

        .add{

            &__title{
                font-size: 0.8em;
                padding: 0em 0em 1em 2em;
                color: $theme-color;
            }

            &__tbox{
                width: auto;
                background-color: #ffffff;
                padding: .5em 0em;
                color: var(--gray);
                text-align: center;
                border-radius: 0.5em;
                border: 1px solid rgb(204, 231, 247);
                font-size: 1.2em;
            }
        }
    }
}