.selection{
    &__list {
        button {
            width: 95%;
            margin: 10pt 0pt;
        }

        &--card {
            position: relative;
            padding: 1% 0;
            font-size: $body-text;
            color: var(--gray);

            .border {
                width: 5pt;
                background-color: $theme-color;
                height: 100%;
                float: left;
                border-top-right-radius: 2.5pt;
                border-bottom-right-radius: 2.5pt;
                position: absolute;
                top: 0;
            }
            &.selected{
                opacity: 1;
                background-color: rgb(204, 231, 247);
                color: $theme-color;
                transition: 400ms;
            }
            &.sub{
                padding:0;
            }
            .display{
                display: none;
            }
        }

        &--content {
            margin: 11pt 21pt;
            font-size: $body-text;
            

            .arrow-down {
                float: right;
                margin: 5pt 0;
                height: 8pt;
                width: 10%;
            }

            .arrow-up {
                float: right;
                margin: 5pt 0;
                height: 8pt;
                width: 10%;
                -webkit-transform: scaleY(-1);
                transform: scaleY(-1);
            }

            &--sub {
                    margin: 0% 8%;
                    position: relative;
                    &.companion{
                        margin: 0;
                        margin-right: 8%;
                        height: 4em;

                    }
                input {
                    display: none;
                }
                
                


                .radio {
                    display: inline-block;
                    padding: 15px 22px 15px 0pt;
                    cursor: pointer;
                    width: 100%;
                    color: var(--gray);
                    &.companion{
                        padding: 22px 22px 22px 0pt;
                    }
                }               
                    
                

                .radio .selection__checkbox {
                    position: relative;
                    line-height: 22px;
                    float: right;
                }

                .radio .selection__checkbox:before,
                .radio .selection__checkbox:after {
                    content: '';
                }


                .radio .selection__checkbox:before {
                    border: 1px solid $theme-color;
                    width: 15pt;
                    height: 15pt;
                    // margin-right: 29px;
                    display: inline-block;
                    vertical-align: top;
                    border-radius: 3pt;
                }

                .radio .selection__checkbox:after {
                    background: $theme-color;
                    width: 8pt;
                    height: 8pt;
                    position: absolute;
                    top: 6px;
                    left: 6px;
                    transition: 300ms;
                    opacity: 0;
                    
                }
                .selection__text{
                    margin-left: 20pt;
                }

                .radio input:checked ~ .selection__checkbox:after {
                    opacity: 1;
                }
                .radio input:checked ~ .selection__text {
                    color: $theme-color;
                    opacity: 1;
                    transition: 400ms;
                }
                .radio input:checked ~ .border.display {
                    display: block;
                    opacity: 1;
                    transition: 400ms;
                }
                
            }
        }
    }

}