.lensproposal {
    height: 85vh;
    width: 100vw;
    position: absolute;
    bottom: 0;
    background-color: $dark;

    &__up {
        background-color: white;

        &.close {
            z-index: 2;
            border-bottom-right-radius: 1.5em;
            border-bottom-left-radius: 1.5em;
            position: absolute;
            top: -7em;
            width: 100%;

            .overlay {
                display: block;
            }
        }

        .overlay {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(255, 255, 255, 0.5);
            border-bottom-right-radius: 1.5em;
            border-bottom-left-radius: 1.5em;
            z-index: 9;
            cursor: pointer;
            inset: 0;
            height: 84vh;
            display: none;
        }
    }

    &__experience {
        // display: flex;     
        display: none;
        background-color: $dark;
        align-items: center;
        justify-content: center;

        &.open {
            display: flex;
            position: absolute;
            bottom: 0;
            width: 100%;
            z-index: 10;
            bottom: 0;

            .modal-content {
                text-align: -webkit-left;

                .btn-header {
                    display: flex;

                    .icon-btn {
                        margin-left: auto;
                        padding-right: 3em;
                    }
                }

                h2.lpb-modal-header {
                    margin: 2em 2em 1.5em 2em;
                    text-align: left;

                    &.invalid {
                        margin: 2em;
                        text-align: center;
                    }
                }

                .text-btn-grp {
                    display: flex;
                    margin: 0 2em;

                    .compact {
                        height: 3.5em;
                    }

                    .lpb-id-tbox {
                        width: 100%;
                        margin: 0;
                    }
                }
            }
        }
    }


    .buttonsGrp {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        height: 74vh;
    }


    .icon {
        svg {
            height: 29pt;
        }
        margin-left: 15pt;
        margin-top: 5pt;
    }

    .lens-footer {
        display: flex;
        font-size: $sub-text;
        margin-top: 10pt;
        padding: 0 12pt;
        height: 9vh;

        strong {
            margin: 5pt 0;
        }

        &__id {
            display: flex;
            margin-right: auto;
            align-items: center;
            justify-content: center;
        }

        &__exp {
            display: flex;
            z-index: 10;
            align-items: center;
            justify-content: center;

            .icon-btn {
                display: contents;
                margin: 5pt 0;
                margin-left: 15pt;
            }
        }
    }

    .modal.lpb-modal-content{
        width: 35%;
        height: 25%;
    }

    .modal-content {
        text-align: -webkit-left;

        .btn-header {
            display: flex;

            .icon-btn {
                margin-left: auto;
                padding-right: 3em;
            }
        }

        h2.lpb-modal-header {
            margin: 2em 2em 1.5em 2em;
            text-align: left;

            &.invalid {
                margin: 2em;
                text-align: center;
            }
        }

        .text-btn-grp {
            display: flex;
            margin: 0 2em;

            .compact {
                height: 3.5em;
            }

            .lpb-id-tbox {
                width: 100%;
                margin: 0;
            }
        }
    }
}