.general{
    &__progress{
        height: auto;
        padding-left: 0em;
        display: flex;

        button.icon-btn{
            transform: scaleX(-1);
            -moz-transform: scaleX(-1);
            -webkit-transform: scaleX(-1);
            -ms-transform: scaleX(-1);
            margin-left: 1.5em;
            margin-right: 2em;
        }

        .step{
            font-family: 'Noto Sans', sans-serif;
            font-size: 0.8em;
            font-weight: bolder;
            color: $theme-color;
        }
    }

    &__body{
        .question-container{
            .sub-question{
                color: $inactive-color;
            }
        }
    }

    &__nextButton{
        position: absolute;
        bottom: 0;
        right: 0;
        padding: 2em;
    }
}