.dnd{
    &__numbered-boxes{
        display: flex;
        text-align: center;
        
        .numbered-box{
            flex: 1 1 0;
            padding: 1em;
            margin: 0.5em;
            border: 1px solid $border-light-blue;
            border-radius: 1em;
            color: $theme-color;
            height: 4em;
            font-size: 1.5em;
        }
    }

    &__text-boxes{
        text-align: left;
        display: flex;
        .text-box{
            flex: 1 1 0;
            padding: 1em;
            margin: 0.5em;
            border: 1px solid $theme-color;
            border-radius: 1em;
            color: $theme-color;
        }
    }
}