.package-offer {
    box-sizing: border-box;
    padding: 20px 35px;
    height: calc(100vh - 127px);
    display: flex;
    flex-flow: column;
    align-items: center;


    &__buttons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;
        width: 100%;

        button {
            margin: 0;
        }
    }

    &__cards {
        flex: 1 1;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        grid-gap: 2rem;
        width: 100%;
        max-width: 960px;
    }

    &__card {
        border-radius: 15px;
        font-size: 2rem;
        font-weight: 700;
        background-size: cover;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-flow: column;
        overflow: hidden;
        transition: all .25s ease-in-out;

        &-info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            padding: 20px;
            box-sizing: border-box;

            label {
                font-size: 14px;
                background-color: $theme-color;
                color: #fff;
                padding: 0 20px;
                border-radius: 20px;
                height: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 12px;
            }

            div {
                width: 49px;
                height: 39px;
                border: 1px solid #fff;
                border-radius: 7px;
                display: flex;
                align-items: center;
                justify-content: center;

                img{
                    width: 26px;
                }
            }
        }

        &-details {
            height: 55%;
            width: 100%;
            background-color: hsla(0,0%,100%,.2);
            backdrop-filter: blur(15px);
            transition: all .25s ease-in-out;
        }

        &-product {
            padding: 25px 25px; 
            border-bottom: 1px solid rgba(#fff, 0.25);

            span{
                height: 40px;
                width: 88px;
                border-radius: 8px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 15px;
                font-weight: normal;
                color: #fff;
                border: 1px solid #fff;
                margin-bottom: 20px;
                position: relative;

                &::after{
                    content: "*";
                    position: absolute;
                    top: 0;
                    right: -12px;
                    font-size: 10px;
                }
            }

            h2{
                font-size: 18px;
                color: #fff;
                margin: 0;
            }
        }

        &-lenses {
            list-style: none;
            margin: 0;

            li {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                margin: 12px 0;

                span{
                    height: 18px;
                    width: 8px;
                    border-radius: 20px;
                    border: 1px solid #fff;
                    display: inline-block;
                    background-color: rgba(#fff, .8);
                    margin-right: 20px;
                    position: relative;
                    z-index: 3;

                    &::before {
                        content: '';
                        height: 18px;
                        width: 8px;
                        border-radius: 20px;
                        border: 1px solid rgba(#fff, 0.3);
                        display: inline-block;
                        background-color: rgba(#fff, 0.3);
                        position: absolute;
                        top: -1px;
                        right: -7px;
                        z-index: 2;
                    }

                    &::after {
                        content: '';
                        height: 18px;
                        width: 8px;
                        border-radius: 20px;
                        border: 1px solid rgba(#fff, 0.1);
                        display: inline-block;
                        background-color: rgba(#fff, 0.1);
                        position: absolute;
                        top: -1px;
                        right: -12px;
                        z-index: 1;
                    }
                }

                label{
                    height: 23px;
                    color: #fff;
                    font-size: 12px;
                    display: inline-flex;
                    align-items: center;
                    border: 1px solid #fff;
                    border-radius: 4px;
                    padding: 0 10px;
                }

                &:first-child + li {
                    span{
                        background-color: rgba(#fff, .3);
                        border-color: rgba(#fff, .3);

                        &::before{
                            background-color: rgba(#fff, .8);
                            border-color: rgba(#fff, .8);    
                        }

                        &::after{
                            background-color: rgba(#fff, .1);
                            border-color: rgba(#fff, .1);    
                        }
                    }
                }

                &:last-child {
                    span{
                        background-color: rgba(#fff, .1);
                        border-color: rgba(#fff, .1);

                        &::before{
                            background-color: rgba(#fff, .3);
                            border-color: rgba(#fff, .3);    
                        }

                        &::after{
                            background-color: rgba(#fff, .8);
                            border-color: rgba(#fff, .8);    
                        }
                    }
                }

            }
        }


        &--standard {
            background-image: url('../../../assets/images/package-standard.png');

            .package-offer{
                &__card{
                    &-info {
                        label {
                            background: rgb(89,87,162);
                        }
                    }
                }
            }
        }

        &--optimized {
            background-image: url('../../../assets/images/package-optimized.png');

            .package-offer{
                &__card{
                    &-info {
                        label {
                            background: rgb(95,190,180);
                        }
                    }
                }
            }
        }

        &--personalized {
            background-image: url('../../../assets/images/package-personalized.png');

            .package-offer{
                &__card{
                    &-info {
                        label {
                            // background: rgb(89,87,162);
                        }
                    }
                }
            }
        }

        &--selected {

            &.package-offer__card--standard{
                box-shadow: 0px 0px 0px 5px rgb(89,87,162);
                .package-offer__card-details {
                    background-color: rgba(89,87,162,0.5);     
                }
            }

            &.package-offer__card--optimized{
                box-shadow: 0px 0px 0px 5px #5fbeb4;
                .package-offer__card-details {
                    background-color: rgba(#5fbeb4,0.5);     
                }
            }
            &.package-offer__card--personalized{
                box-shadow: 0px 0px 0px 5px #0386D6;
                .package-offer__card-details {
                    background-color: rgba(#0386D6,0.5);     
                }
            }

        }
    }
    
}