.transition-container{
    position: relative;
    .demo {
            &--three {
                position: absolute;
                left: 10%;
                opacity: 0.7;
            }

            &--two {
                position: absolute;
                left: 18%;
                opacity: 0.3;
                    }
                
                }
}
#background-image {
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0;
    right: 0;
}

#product-shape {
    fill: rgba(0,0,0,0.2);
}

#product-svg {
    position: absolute;
    left: 2em;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 50%;
    mix-blend-mode: multiply;
}

.left-container{
    &__left {
        flex: 0;
        -webkit-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
        -moz-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
        box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
        border-radius: 15pt;
        height: 100%;

        .border {
            width: 5pt;
            background-color: $theme-color;
            height: 100%;
            float: left;
            border-top-right-radius: 25pt;
            border-bottom-right-radius: 25pt;
            -webkit-box-shadow: 5px 0px 11px 0px rgba(0, 134, 214, 0.2);
            -moz-box-shadow:5px 0px 11px 0px rgba(0, 134, 214, 0.2);
            box-shadow: 5px 0px 11px 0px rgba(0, 134, 214, 0.2);
        }

        &--header {
            margin: 30pt 25pt;
            font-size: $body-text;
            display: inline-flex;
            strong{
                text-transform: capitalize;
                margin: 0 7%;
            }
            .shp0{
                fill: #ffffff !important;
            }

            .dot {
                height: 10pt;
                width: 10pt;
                background-color: transparent;
                border-radius: 50%;
                display: inline-block;
                margin: 2px 9px -1px 0px;
                border: 1px solid rgba(90, 102, 117, 0.1);
            }

            .icons {
                float: right;
                display: inline-flex;
                margin-left: 26%;
            }

            .vl {
                border-left: 1px solid var(--gray);
                height: 13pt;
                opacity: 10%;
                margin: 0 15pt;
                margin-top: 4pt;
            }
        }

        &--demo {
            margin: 0 35pt;
            height: 73%;
            position: relative;
            .prescription-display{
                .demo {
                    &--one {
                        position: absolute;
                        left: 10%;
                        opacity: 0.7;
                    }

                    &--two {
                        position: absolute;
                        left: 18%;
                        opacity: 0.3;
                    }                
                }
            }
            .correct-display{
                .demo {
                    &--one {
                        position: absolute;
                        left: 10%;
                    }

                    &--two {
                        position: absolute;
                        left: 18%;
                    }                
                }
            }
            
            .protect-display{
                .demo {
                    &--three {
                        position: absolute;
                        left: 10%;
                    }

                    &--two {
                        position: absolute;
                        left: 18%;
                    }
                
                }
            }
            .enhance-display{
                .demo {
                    &--one {
                        position: absolute;
                        left: 10%;
                    }

                    &--three {
                        position: absolute;
                        left: 18%;
                    }
                
                }
            }
        }
        &--options{
            display: flex;
            float: right;
            margin-right: 2em;
            .demo-btn{
                border: 1pt solid rgba(0, 134, 214, 0.2);
                background-color: white;    
                
                .text{
                    padding-top: 0.2em !important;
                    color: $theme-color !important;
                    font-weight: 300 !important;
                }
            }
            .save-btn{
                border: 1pt solid rgba(0, 134, 214, 0.2);
                background-color: $theme-color;    
                img{
                    padding-top: 0.3em;
                }
                .text{
                    padding-top: 0.2em !important;;
                    color: white !important;
                }
            }
        }
    }
}