.summary-product{
    &__container{
        color: $light-gray;
        height: 50%;
        grid-area: desc1;
        &.two{
            grid-area: desc2;
        }
    }
    &__header{
        background-color: $summary-box;        
        font-size: 0.75em;
        text-align: center;
        height: 20px;
        padding: 0.5em 0;
    }
    &__description{
        display: flex;
        text-align: center;
        justify-content: center;
        .sub{            
            font-size: 0.65em;
        }
        .product{
            margin: 0 0.35em;
            width: 30%;
        }
        svg{
            width: 2em;
            height: 2.5em;
        }
    }
    
}