.navbar{
    z-index: 100;
    overflow: hidden;
    padding: 15px 0px 0px;         
    display: flex;
    position: sticky;
    background-color: white;
    top: 0;
    height: 75px;
    
    h2{
    font-family: 'AkhandSoft-Bold', Arial, sans-serif;
    font-weight:normal;
    font-style:normal;
    font-size: 1.5625em;
    color: $gray;
    margin: 0;
    }
    a, span{
        float: left;
        color: #f2f2f2;
        text-align: center;
        padding: 0;
        text-decoration: none;
        font-size: 17px;
    }
    &__right{
        float: right;
        img{
            height: 20pt;
            width: 100%;
        }
        a, span{
            margin: 12pt 10pt;
        }
        span{
            margin: 17pt 10pt;
        }
        .vl{
            border-left: 1px solid $gray;
            height: 9pt;
            opacity: 10%;
        }
        .close-gray-icon{
            height: 30pt;
        }
        .exit-btn{
            margin: 10pt;
        }
    }
    &__center{        
        margin: 0 auto;   
        justify-content: center;
        padding: 8pt 0;
    }
    &__logo{
        margin-right: 14%;
    }
    
}