.category-list{
    ul{
        text-align: center;
        list-style-type: none;
        padding: 0;

        li{
            margin: 2em 0em;
            height: auto;
            color: $inactive-color;
        }

        li.active{
            background-color: $theme-color;
            color: #ffffff;
            font-weight: 600;
            width: fit-content;
            margin: 1em auto;
            padding: 0.5em 2em;
            border-radius: 1em;
        }
    }
}