/* MAIN */
html, h2, code {
    scroll-behavior: smooth;
    font-family: 'Noto Sans', sans-serif ;
}

body{
    margin: 0;
    padding: 0;
}

code{
    font-weight: 700;
}

.dim.showDim{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 110;
    background-color: #000000;
    opacity: 0.3;
}

.save-modal-content{
        .btn-header{
            display: flex;
            .icon-btn{
                margin-left: auto;
                padding-right: 3em;
        }
    }
    .lpb-modal-header{        
        text-align: center;
        margin-top: 20%;
    }
    .save-text-grp{
        display: flex;
        justify-content: center;
        margin-top: 3em;
        h2.text{
            padding:0;
        }
        .compact{
            width: 7em;
            justify-content: center;
        }
    }
    &.one-line{
        text-align: center;
        width: 100%;
        height: 100%;
        svg{
            margin-top: 25%;
        }
    }

}

/* width */
    ::-webkit-scrollbar {
        // width: 5px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
        background: rgba(90, 102, 117, 0.1);
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
        background: $theme-color;
        // border-radius: 10px;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: $theme-color;
    }
