.measures-tables{
    display: flex;

    &__table{
        -webkit-flex: 1 0 0px;
        flex: 1 0 0px;
        margin: 1em;
        border: 1px solid $border-light-blue;
        border-radius: 0.5em;

        &--header{
            padding: .8em 1em;
            border-radius: 0.5em;
            background-color: $border-light-blue;
            border-bottom-right-radius: 0em;
            border-bottom-left-radius: 0em;
            color: $theme-color;
            font-weight: bold;
        }

        &--content{
            padding: 1.5em 1em;

            .group{
                display: flex;
                margin-bottom: 0.5em;

                .field-name{
                    color: $theme-color;
                    min-width: 6em;
                    font-size: 10pt;
                }

                .field-value{
                    margin-left: auto;
                    color: $theme-color;
                    border: 1px solid $theme-color;
                    padding: 0.25em 1em;
                    min-width: 4em;
                    border-radius: 0.25em;
                    font-size: 10pt;
                }
            }
        }
    }

    &__table:first-child,
    &__table:last-child{
        margin-right: 0;
        margin-left: 0;
    }
}