.radio-button-prescription{
    .rdb-group{
        border: 1px solid $border-light-blue;
        border-radius: 1em;

        .selection__list--card.sub{
            border-radius: 0em;

            .selection__list--content--sub{
                margin: 0;

                .radio{
                    width: -webkit-fill-available;

                    .selection__text{
                        color: $dark;
                    }
                }

                .radio input:checked {
                    background-color: $border-light-blue;
                }
            }
        }
    }

    .rdb-group{
        .selection__list--card.sub:first-child{
            .selection__list--content--sub{
                .radio{
                    border-top-left-radius: 1em;
                    border-top-right-radius: 1em;
                }
            }
        }
    }

    .rdb-group{
        .selection__list--card.sub:last-child{
            .selection__list--content--sub{
                .radio{
                    border-bottom-left-radius: 1em;
                    border-bottom-right-radius: 1em;
                }
            }
        }
    }
}