.need-explorer{
    height: 85vh;
    width: 100vw;
    display: flex;
    position: absolute;
    bottom: 0;

    &__modal-page{
        background-image: url('../../assets/images/needexplorer-hero.jpg');
        background-size: 100% auto;
        height: 85vh;
        width: 100vw;
        display: flex;
        position: absolute;
        bottom: 0;

        .modal .content{
            display: contents;
    
            &__header{
                height: auto;
                padding: 1em;
                padding-left: 0em;
                display: flex;
    
                button.icon-btn{
                    transform: scaleX(-1);
                    -moz-transform: scaleX(-1);
                    -webkit-transform: scaleX(-1);
                    -ms-transform: scaleX(-1);
                    margin-left: 1.5em;
                    margin-right: 2em;
                }
    
                .step{
                    font-family: 'Noto Sans', sans-serif;
                    font-size: 0.8em;
                    font-weight: bolder;
                    color: $theme-color;
                }
            }
    
            &__body{
                height: auto;
                width: 100%;
                padding-left: 0em;
    
                .title{
                    font-family: 'Noto Sans', sans-serif;
                    font-size: 1.5em;
                    font-weight: 600;
                    padding-bottom: 0.5em;
                    color: rgb(47, 59, 74);
                }
    
                .subtitle{
                    color: rgba(90, 102, 117, 0.6);
                    text-align: justify;
                }
            }
    
            &__footer{
                height: auto;
                position: absolute;
                bottom: 2em;
                right: 2em;
    
                .btn-grp{
                    display: flex;
    
                    button:last-child{
                        margin-right: 0;
                    }
                }
            }
        }
    }
}