.title-header{
    text-align: center;
    border-top: 2px solid $line-gray;
    margin: 2em 0em 0em 0em;

    &__header-text{
        color: $line-gray;
        text-align: center;
        width: -moz-fit-content;
        width: fit-content;
        background-color: #ffffff;
        padding: 0.2em 2.8em;
        display: -webkit-inline-box;
        top: initial;
        position: relative;
        top: -1em;
        font-size: 10pt;
    }
}