.legal-notices{
    overflow-x: unset;
    height: 80vh;
    width: -webkit-fill-available;
    position: absolute;
    bottom: 0;
    overflow-y: auto;
    padding: 0em 2em 2em 10em;

    .section{
        display: flex;

        .number-bullet{
            margin-right: 3em;
            margin-top: 0.6em;
            font-size: 10pt;
            color: $theme-color;
            font-weight: 600;
        }

        .section-content{
            &__header{
                margin: 0;
                color: $dark;
            }

            &__paragraph{
                color: $light-gray;
                font-size: 11pt;

                span{
                    display: block;
                }
            }
        }
    }
}