@font-face {
    font-family: "AkhandSoft-Bold";
    src: url("../assets/font/AkhandSoft-Bold.otf") format("opentype");
}

$gray: #2f3b4a;
$dark:rgb(47,59,74);
$theme-color: #0386D6;
