.delivery{
    height: 85vh;
    width: 100vw;
    display: flex;
    position: absolute;
    bottom: 0;
    transition: margin .5s;
    margin: 0 0 0 0px;

    &__modal-page{
        background-image: url('../../../assets/images/delivery/hero.jpg');
        background-size: 100% auto;
        height: 85vh;
        width: 100vw;
        display: flex;
        position: absolute;
        bottom: 0;

        &--modal-content{
            padding: 3em 0em;
            color: $dark;

            .content-header{
                color: $dark;
                text-align: center;
            }

            .content-body{
                .item{
                    display: flex;
                    border-bottom: 1px solid $inactive-color;
                    margin: 0em 5em;

                    .label{
                        text-align: center;
                        border: 1px solid $dark;
                        height: 2em;
                        border-radius: 0.5em;
                        margin: 1.5em 3em 1em 0em;
                        padding: 0.5em 1em 0em;
                        font-weight: bold;
                        font-size: 10pt;
                    }

                    .label.no-border{
                        border: none;
                        text-align: left;
                        font-size: 17pt;
                        padding: 0;
                        margin: 1em 0em 0em;
                        font-weight: 100;
                    }

                    .item-price{
                        margin-left: auto;
                        font-size: 17pt;
                    }
                }

                .item:last-child{
                    border-bottom: none;
                }
            }

            .content-footer{
                position: absolute;
                bottom: 0;
                right: 0;
                margin: 3em 4em;
                height: 3em;
                display: flex;
            }
        }
    }

    &__summary-page{
        position: absolute;
        z-index: 100;
        height: 0vh;
        width: 100vw;
        bottom: 0;
        transition: height .5s ease-in-out;
        overflow-y: auto;

        &--navbar{
            position: fixed;
            height: 5em;
            background-color: #ffffff;
            border-top: 1px solid $inactive-color;
            border-bottom: 1px solid $inactive-color;
            width: 100%;
            display: flex;
            z-index: 100;

            ul{
                list-style-type: none;
                display: flex;
                padding: 0 2em;
                margin: 1.8em 0em;
                
                li{
                    padding: 0em 2em;
                    color: $dark;
                    font-size: 14px;
                    padding-top: 2px;
                }

                li.active{
                    background-color: $theme-color;
                    border-radius: 1em;
                    color: #ffffff;
                    transition: background-color .3s ease-out; 
                }

                li button.back-modalPage{
                    transform: rotate(90deg);
                }
            }

            .btn-grp{
                margin-left: auto;
                padding: 1em;
                display: flex;

                .price-label{
                    color: $dark;
                    font-size: 15pt;
                    margin: 0em 0.5em;
                    border: 1px solid $inactive-color;
                    border-radius: 0.5em;
                    padding: 0.5em 1.2em;
                }

                .saveExport{
                    height: auto;
                    padding: 0em 2em;
                    margin: 0.7em 1em;
                    width: auto;
                }
            }
        }

        &--body{
            overflow-y: scroll;
            height: auto;
            scroll-behavior: smooth;
            -webkit-overflow-scrolling: touch;
        }
        
        &--body .section{
            height: auto;
            background-color: #ffffff;
            padding: 7em 6em;

            .prescription-content{
                display: flex;

                .left{
                    min-width: 35%;
                    padding-top: 5em;

                    img{
                        width: 25em;
                    }
                }

                .right{
                    width: 65%;
                    padding: 1em 7em;

                    .header{
                        color: $dark;
                    }

                    .subHeader{
                        color: $light-gray;
                    }

                    .add{
                        margin-top: 1em;

                        &__title{
                            font-size: 0.8em;
                            padding: 0em 0em 1em 2em;
                            color: $theme-color;
                        }
            
                        &__tbox{
                            width: auto;
                            background-color: #ffffff;
                            padding: .5em 0em;
                            color: var(--gray);
                            text-align: center;
                            border-radius: 0.5em;
                            border: 1px solid rgb(204, 231, 247);
                            font-size: 1.2em;
                            margin-right: 4%;
                        }
                    }
                }
            }

            .pair1-content{
                .pair1-info{
                    display: inline-block;
                    position: relative;
                    width: 100%;

                    .hero{
                        width: 100%;
                    }
    
                    .content-overlay{
                        width: 60%;
                        top: 0;
                        position: absolute;
                        right: 0px;
                        bottom: 5px;
                        border-top-right-radius: 1em;
                        border-bottom-right-radius: 1em;
                        background-color: rgba(0, 0, 0, 0.4);
                        -webkit-backdrop-filter: blur(15px);
                        backdrop-filter: blur(15px);

                        .price-label{
                            border: 1px solid #ffffff;
                            width: auto;
                            padding: 0.5em 1em;
                            border-radius: 1em;
                            color: #ffffff;
                            position: absolute;
                            right: 0;
                            margin: 1em 1.5em;
                            font-size: 14pt;
                            background-color: rgba(255,255,255, 0.1);
                        }

                        .title{
                            display: flex;
                            color: #ffffff;
                            margin: 2em 4em 0em 4em;

                            .headerLabel{
                                margin: 2.2em;
                                border: 1px solid #ffffff;
                                padding: 0.5em 1.3em;
                                border-radius: 0.5em;
                                font-size: 10pt;
                            }
                        }

                        .title-content{
                            color: #ffffff;
                            margin: 0em 4em;
                        }

                        .overlay-icons{
                            display: flex;
                            margin-top: 4em;

                            .icon-text{
                                padding: 0.3em 2em;
                            }

                            .icon-text:nth-child(2){
                                border-left: 1px solid #3c3c3c;
                                border-right: 1px solid #3c3c3c;
                            }
                        }
                    }
                }

                .pair1-lens-consultation{
                    display: flex;
    
                    .left{
                        min-width: 35%;
                        padding-top: 5em;

                        .lens-display{
                            .demo {
                                &--one {
                                    position: absolute;
                                    left: 10%;
                                    opacity: 0.7;
                                }
            
                                &--two {
                                    position: absolute;
                                    left: 18%;
                                    opacity: 0.3;
                                }      
                                
                                &--three {
                                    position: absolute;
                                    left: 14%;
                                    opacity: 0.3;
                                }      
                            }
                        }
                    }

                    .right{
                        width: 65%;
                        padding: 1em 7em;

                        .header{
                            color: $dark;
                        }
    
                        .subHeader{
                            color: $light-gray;
                        }
    
                    }
                }

                .pair-measures{
                    display: flex;
    
                    .left{
                        min-width: 35%;
                        padding-top: 3em;

                        .carousel .carousel__slider{
                            border-radius: 1em;
                        }

                        .carousel .button-group{
                            margin-top: 10px;
                            text-align: center;

                            button,
                            button[disabled]{
                                all: unset;
                                background: lightblue;
                                border-radius: 50%;
                                width: 30px;
                                height: 30px;
                                -webkit-text-fill-color: $theme-color;
                                color: $theme-color;
                                background-color: rgb(241,241,241);
                                font-size: 11pt;
                                text-align: center;
                            }
                            
                            .backBtn{
                                margin-right: 1em;
                            }

                            .nextBtn{
                                margin-left: 1em;
                            }
                        }
                    }

                    .right{
                        width: 65%;
                        padding: 1em 0em 1em 7em;

                        .header{
                            color: $dark;
                        }
    
                        .subHeader{
                            color: $light-gray;
                        }

                        .reference-field{
                            display: flex;
                            margin-top: 3em;
                            border-radius: 0.5em;
                            border: 1px solid $border-light-blue;

                            &__number{
                                padding: 1em 2em;
                                background-color: $border-light-blue;
                                color: $theme-color;
                                border-radius: 0.5em;
                                border-bottom-right-radius: 0;
                                border-top-right-radius: 0;
                            }

                            &__value{
                                padding: 1em;
                                color: $dark;
                            }

                            &__price{
                                margin-left: auto;
                                padding: 1em 1.5em;
                                border-left: 1px solid $border-light-blue;
                                color: $theme-color;
                            }
                        }
                    }
                }
            }

            .vision-test{
                &__content{
                    display: flex;

                    &--board{
                        text-align: center;
                        -webkit-flex: 1 0 0px;
                        flex: 1 0 0px;
                        border: 1px solid $border-light-blue;
                        border-radius: 1em;
                        margin: 2em;
                        padding: 5em 0.5em;
                        min-height: 30vh;

                        .vision-board.lrg{
                            width: 8em;
                        }

                        .vision-board.mdm{
                            width: 5em;
                        }

                        .vision-board.sm{
                            width: 3em;
                        }

                        h3{
                            margin-top: 3em;
                            color: $theme-color;
                        }
                    }
                }
            }
        }
    }

    #Advices{
        height: 600px;
    }

    &__summary-page.summaryOpen{
        height: 85vh;
        width: 100vw;
        position: absolute;
        bottom: 0;
    }

    &__modal-page.dimBG{
        -webkit-filter:brightness(30%);
        -moz-filter:brightness(30%);
        filter: url(#brightness);
        filter:brightness(30%);
        transition: filter .25s ease-in-out;
    }

    &__export-modal{
        width: 50% !important;
        height: 25% !important;
        z-index: 120;

        .modal__content {

            &--header {
                display: flex;
                position: relative;
                padding-top: 2em;
    
                .title {
                    font-size: $header-text;
                    color: $dark;
                    margin-left: 10%;
                }
    
                .icon-btn {
                    position: absolute;
                    top: 0;
                    right: 5%;
                }
            }
    
            &--btn {
                display: flex;
                color: $theme-color;
                height: 3em;
                justify-content: center;
    
                .summary-btn.export {
                    border: 1pt solid $theme-color;
                    padding: 1em;
                }
    
                .next-btn {
                    flex-direction: unset;
                }
    
                img {
                    width: 20pt;
                    height: 100%;
                    position: absolute;
                    right: 6%;
                    top: 0;
                }
            }
        }
    }
}