.lens-thickness {

    display: flex;
    color: $theme-color;
    height: 85vh;
    width: 100vw;
    position: absolute;
    bottom: 0;

    a{
        text-decoration: none;
        color: $theme-color;
    }

    &__right {
        flex: 1;
        margin-left: 11pt;
    }
    .lens-thickness-icon{
        height: 92%;
    }
    
    .left-container__left--demo{
        margin: 0 50pt;
    }
    .left-container__left--header .icons{
        margin-left: 26%;
    }

    
}
