.message-container{
    border: 1px solid $border-light-blue;
    border-radius: 8pt;
    max-width:21%;
    position: absolute;
    padding: 20pt;
    height: 20%;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    .highlight{
        background-color: $theme-color;
        color: #ffffff;
        width: 70%;
        padding: 2%;
        text-align: center;
        border-radius: 4pt;
        margin-bottom: 10pt;
        font-size: $small-text;
    }
    .text{
        font-weight: 700;
        font-size: $body-text;
        color: var(--gray);
        margin-right: 10pt;
        margin-bottom: 18pt;

    }
    .i-start-arrow{
        position: absolute;
        right: 9%;
        bottom: 9%;
        width: 20pt;
    }
}