.need-explorer-container{
    margin: 2em;
    height: -webkit-fill-available;
    width: -webkit-fill-available;
    display: flex;
    position: absolute;
    bottom: 0;

    &__left-panel{
        display: grid;
        width: 30%;

        .progress-bar{
            position: absolute;
            bottom: 0;
            width: 21%;
            height: 4.2em;
            background-color: $theme-color;
            color: white;
            padding: 1% 2%;
            border-radius: 8pt;
            background-color: $theme-color;
            -webkit-box-shadow: 3px 4px 20px 3px rgba(0, 136, 214, 0.20);
            -moz-box-shadow: 3px 4px 20px 3px rgba(0, 136, 214, 0.20);
            box-shadow: 3px 4px 20px 3px rgba(0, 136, 214, 0.20);

            strong{
                font-size: $sub-text;
            }
            span{
                font-size: $body-text;
                float: right;
            }
            progress{
                width: 100%;
                border-radius: 5pt;
                height: 12%;
                margin-top: 10%;
                
            }
            progress[value] {
                appearance: none;
                -webkit-appearance: none;
            }
            progress[value]::-webkit-progress-bar {
                width: 100%;
                background-color: $background-progress;
                border-radius: 5pt;
                
            }
            progress[value]::-webkit-progress-value {
                background-color: white;
                border-radius: 5pt;
            }
            
        }
    }

    &__right-panel{
        width: 70%;
        border-radius: 1em;
        -webkit-box-shadow: 0px 0px 103px -45px rgba(0,136,214,0.4);
        -moz-box-shadow: 0px 0px 103px -45px rgba(0,136,214,0.4);
        box-shadow: 0px 0px 103px -45px rgba(0,136,214,0.4);
        padding: 2em;
        
        .answers{
            &__grid{
                display: grid;
                grid-template-columns: repeat(3, 1fr);  
                grid-template-rows: repeat(2, 35%);
            }
            &__grid--four{
                display: grid;
                grid-template-columns: repeat(2, 1fr);  
                grid-template-rows: repeat(2, 19vh);
                grid-gap: 13pt;
            }
        }
        
    }

    &__result-left{
        display: inline;
        width: 40%;
        height: 50%;
        border-radius: 1em;
        -webkit-box-shadow: 0px 0px 103px -45px rgba(0,136,214,0.4);
        -moz-box-shadow: 0px 0px 103px -45px rgba(0,136,214,0.4);
        box-shadow: 0px 0px 103px -45px rgba(0,136,214,0.4);
        text-align: center;
        padding: 10% 0;
        margin-right: 3%;
        margin-left: 2%;

        .user-icon {
            padding: 10% 25%;
            
        }
        svg{
            fill: $theme-color;
        }
        
        .result-text{
            text-decoration: underline;
            color: var(--gray);
        }
        .next-btn{
            margin-left: 4.2em;
            text-decoration: none;
            margin-top: 5em;
            margin-bottom: 2em;
        }
    }
    &__result-right{
        width: 60%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);  
        grid-template-rows: repeat(3, 1fr);
        grid-gap: 12pt;
    }

    &__finished{
        width: 30em;
        height: 25em;
        border-radius: 1em;
        box-shadow: 0px 0px 103px -45px rgba(0, 136, 214, 0.4);
        text-align: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) !important;

        svg{
            margin-top: 5em;
        }

        .result-text{
            text-decoration: underline;
            color: var(--gray);
        }
        .next-btn{
            margin: auto;
            margin-bottom: 1em;
            text-decoration: none;
        }
    }
}