.portal-modal {
    background-color: #fff;
    position: fixed;
    z-index: 901;
    top: 50%;
    left: 50%;
    margin: auto;
    border-radius: 15pt;
    transform: translate(-50%, -50%);
    display: flex;
    flex-flow: column;
    overflow: hidden;

    &--large{
        width: calc(100vw - 60px);
        height: calc(100vh - 60px);    
    }

    &__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-sizing: border-box;
        padding: 15px 20px;
        box-shadow:0px 0px 3px 0px rgba(90,102,117, .25);

        > * {
            margin:0;
        }


        .icon-btn.close{
            width:45px;
            height: 45px;
            background: transparent;
            transform: translateX(8px);

            svg {
                height: 100%;
                width: 100%;
            }
        }

        .back-btn {
            width: 105px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            padding:10px 0px;

            h2.text {
                margin:0;
                padding:0;
            }
        }

        &-title {
            font-size: 20px;
            color:$gray;
        }
    }

    &-overlay {
        width:100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        background-color: #0000008c;
        z-index: 900;
    }

    &__content {
        flex: 1;
    }
}