button.blue-bg{
    background-color: $theme-color;
}

button.faded-white-bg{
    background-color: #EFEFEF;
}

button.white-bg{
    background-color: #FFFFFF;
}

button.bordered{
    border: 1px solid $theme-color;
}

button.big-btn{
    h2{
        font-size: 2em;
    }
}

.fluid-icon-btn {
    color: var(--gray);
    border: 2px solid $cerulean;
    background-color: #ffffff;
    border-radius: 1.25em;
}

.basic-icon-btn {
    display: flex;
    flex-direction: row-reverse;
    border-radius: 0.55em;
    border: none;
    padding: 1em 1.8em;
    margin: 0em 0.5em;
    align-items: center;
    justify-content: center;

    h2.text {
        padding: 0em 1em 0em 0em;
        margin: 0;
        text-align: left;
        font-size: 1.2em;
        padding-top: 0.1em;
        color: $theme-color;
    }
}

.basic-icon-btn.simple {
    border: 1pt solid $theme-color;
    background-color: white; 

    h2.text{
        padding: 0;
        font-weight: 100;
    }   
}

.basic-btn {
    border-radius: 0.55em;
    border: none;
    padding: 1em 1.8em;
    margin: 0em 0.5em;

    h2.text {
        height: 1.5em;
        color: $theme-color;
    }
}

.white{
    background-color: #ffffff;
}

.basic-btn:focus {
    background-color: $theme-color;

    h2.text {
        color: #ffffff;
    }
}

.icon-btn {
    border: none;
    width: 0;
    height: 0;
    padding: 0;
    margin: 0;
}

.next-btn {
    display: flex;
    flex-direction: row-reverse;
    border-radius: 0.55em;
    border: none;
    padding: 1em 1.8em;
    margin: 0em 0.5em;
    border: 1pt solid rgba(0, 134, 214, 0.2);
    background-color: $theme-color;

    h2.text {
        padding: 0em 1em 0em 0em;
        margin: 0;
        text-align: left;
        font-size: 1.2em;
        color: white
    }


    img {
        padding-top: 0.14em;
    }

}



.compact{
    padding: 0.8em 1em;
}



.back-btn {
    background-color: white;
    border: 1px solid $inactive-color;
    padding: 1em 0.8em;
    flex-direction: row;
    margin: 2em 0.5em;
    text-transform: capitalize;

    display: flex;
    border-radius: 0.55em;

    h2.text {
        padding: 0em 1em;
        margin: 0;
        text-align: left;
        font-size: 1.2em;
        color: $theme-color;
    }

    img {
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        padding-top: 0.14em;
    }
}


button:focus {
    outline: 0;
}

.price-btn{
    margin: 0;
    border: 1pt solid $theme-color;
    padding: 0.7em 0.8em;
    width: 9.5em;
    &.active{
        background-color: $theme-color;
        h2.text{
            color: white;
            font-weight: bold;
        }
        
    }
    h2.text{
        font-weight: normal;
    }
}

.summary-btn{
    background-color: white;
    border: 1pt solid $theme-color;
    width: 9em;
    position: relative;
    margin: 0 2%;
    padding: 0.7em 1.1em;
    &.cancel{
        border: 1pt solid $border-light-blue;
        width: 4em;
        .icon-border{
            right: 28%;
        }
    }
    &.export{
        border: 1pt solid $border-light-blue;
        svg{
            position: absolute;
            right: 12%;
            top: 30%;
            g{
                stroke-width: 1.5px;
            } 
        }
        &.active{
            background-color: $theme-color;
            h2.text{
                color: white;
            }
            svg g{
                stroke: #ffffff;
                stroke-width: 2px;
            }        
        }
    }
    h2.text{
        color: $theme-color;
        padding: 0;
        font-weight: normal;
    }
    .icon-border {
                height: 14pt;
                width: 14pt;
                background-color: white;
                border-radius: 50%;
                display: inline-block;
                color: $theme-color;
                border: 1pt solid $theme-color;
                position: absolute;
                right:9%;

                .img {
                    margin: 9% 0;
                    svg{
                        width: 7pt ;
                        height: 100%;
                    }
                }
            }
}

.exp-btn {
    background-color: transparent;
    border: 1px solid white;
    padding: 1em;
    flex-direction: row-reverse;
    margin: 2em 1em;
    text-transform: capitalize;
    align-items: inherit;

    display: flex;
    border-radius: 0.55em;
    h2.text {
        padding: 0em 1em;
        margin: 0;
        text-align: left;
        font-size: 1.2em;
        color: white;
    }
    .circle{
        width: 0.5em;
        height: 0.5em;
        border: 1px solid white;
        background-color: transparent;
        border-radius: 50%;
    }
    &.selected{
        border: 1px solid $inactive-color;
        background-color: white;
        h2.text {
            color: $dark;
        }
        .circle{
            background-color: $theme-color;
        }
    }

    img {
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        padding-top: 0.14em;
    }
}

.companion-btn {
    background-color: white;
    border: 1px solid $border-light-blue;
    padding: 1em 0.8em;
    flex-direction: row;
    text-transform: capitalize;
    display: flex;
    border-radius: 0.75em;
    width: 29.5em;
    height: 4.5em;

    &.active{
        background-color: $theme-color;
        -webkit-box-shadow: 0px 0px 19px 7px rgba(0,134,214,0.3);
        position: relative;
        h2.text{
            color: white;
            font-weight: bold;
        }
        svg g path{ 
            fill: white;
        }
        .right{
            position: absolute;
            right: 4em;
            top: 1.85em;
            svg {                
                width: 0.4em;
                height: 100%;
                g path{
                    fill: none;
                }
            }
        }
    }

    h2.text {
        padding: 0em 1em;
        text-align: left;
        font-size: $body-text;
        color: $theme-color;
        font-weight: normal;
        color:$dark;
        margin: auto 0;
    }
    svg{
        width: 3em;
        height: 2em;
        margin: auto 0;
        padding-left: 2em;
    }
    svg g path{ 
        fill: $theme-color;
    }
}
