.toggle-radio {
    width: 3.6em;
    height: 1.8em;
    background-color: #5a6675;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    border-radius: .2em;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    transition: all .5s ease-in-out;
    
    &__slide {
        width: 1.6em;
        height: 1.2em;
        background-color: #fff;
        transform: translateX(.3em);
        color: #0386D6;
        transition: all .5s ease-in-out;
        border-radius: .2em;
        
        //extend flex-center;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        &::after, &::before{
            content: '';
            width: 1px;
            height: .6em;
            background-color: #0386D6;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            transition: all .25s ease-in-out;
        }

        &::after{            
            transform: rotate(45deg);
        }
        &::before{    
            transform: rotate(-45deg);
        }        
    }

    &--on {
        background-color: #0386D6;

        span {
            transform: translateX(1.7em);

            &::before{    
                height: .3em;
                transform: rotate(-45deg) translate(-.3em, -.1em);
            }
        }
    }
}