.accordion{
    &__list{
        &--card {
            position: relative;
            padding: 1% 0;
            font-size: $body-text;
            color: var(--gray);

            .border {
                width: 5pt;
                background-color: $theme-color;
                height: 100%;
                float: left;
                border-top-right-radius: 2.5pt;
                border-bottom-right-radius: 2.5pt;
                position: absolute;
                top: 0;
            }
            &.selected{
                opacity: 1;
                background-color: rgb(204, 231, 247);
                color: $theme-color;
                transition: 400ms;
            }
            &.sub{
                padding:0;
            }
            .display{
                display: none;
            }
        }

        &--content {
            margin: 11pt 21pt;
            font-size: $body-text;

            
            

            .arrow-down {
                float: right;
                margin: 5pt;
                height: 13pt;
                width: 6%;
            }

            .arrow-up {
                float: right;
                margin: 5pt;
                height: 13pt;
                width: 6%;
                -webkit-transform: scaleY(-1);
                transform: scaleY(-1);
            }
           
        }
    }
}