.text-box-one-border-container{
    display: inline-grid;
    width: 100%;
    color: $theme-color;
    margin: 0.5em 0em;

    &__label{
        color: $dark;
        font-weight: bolder;
    }

    &__tb{
        border: none;
        border-bottom: 2px solid $theme-color;
        padding: 1em 0em;
        color: $theme-color;
        font-size: 14pt;
    }

    &__tb::placeholder{
        color: $theme-color;
    }

    &__tb:focus{
        outline: none;
    }

    .pid-btn{
        position: absolute;
        right: 0;
        margin-right: 5em;
        margin-top: 2em;
    }
}