.modal{
    display: none;
    z-index: 1;
    width: 55%;
    height: 40%;
    overflow: auto;
    background-color: #ffffff;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
    border-radius: 1em;
    padding: 2em;
}

.modal.modal-simple{
    width: 36%;
    height: 36%;
    z-index: 120;
    padding: 0.8em;
    -webkit-box-shadow: 0px 0px 19px 7px rgba(0,0,0,0.24);
    -moz-box-shadow: 0px 0px 19px 7px rgba(0,0,0,0.24);
    box-shadow: 0px 0px 19px 7px rgba(0,0,0,0.24);
}

.modal.show{
    display: block;
}
