.education{
    font-size: 62.5%;
    * {
        box-sizing: border-box;
        padding: 0;
        margin: 0;
    }
}

@import './education-landing';
@import './education-controls';
@import './education-content';
@import './education-step';

.education{
    display: flex;
    height: 100%;

    &__toggle-controls {
        width: 1.7em;
        height: 1.7em;
        padding: 0;
        border: none;
        background-color: #0386D6;
        border-radius: 50%;
        position: absolute;
        top: 1.5em;
        left: 1em;

        transition: all .5s ease-in-out;
        display: flex;
        align-items: center;
        justify-content: center;

        span{
            position: relative;
            height: .1em;
            width: .8em;
            display: block;

            &::after,&::before{
                content: '';
                width: .8em;
                height: .1em;
                background-color: #fff;
                position: absolute;
                left: 0;
                transition: all .25s ease-in-out;
            }

            &::after{
                transform: translateY(-.15em);
            }

            &::before{
                transform: translateY(+.15em);
            }
        }

        &--collapsed {
            left: 1.5em;
    
            background-color: #eeeff1;

            span {
                &::after{
                    background-color: #0386D6;
                }
    
                &::before{
                    background-color: #0386D6;
                }
            }
        }

        
    }
}

