.button {
    flex-grow: 1;
    margin: 10pt;
    flex-basis: 40%;
    &__blue {
        flex: 1;
        background-color: $theme-color;
        color: white;
        border-radius: 15pt;
        padding: 15pt 35pt;
        font-weight: 700;
        -webkit-box-shadow: 0 10px 20px -2px rgba(0, 134, 214, 0.3);
        -moz-box-shadow: 0 10px 20px -2px rgba(0, 134, 214, 0.3);
        box-shadow: 0 10px 20px -2px rgba(0, 134, 214, 0.3);
        

        .center {
            display: flex;
        }

        .content {

            &-left {
                flex: 1;

                &-status {
                    border-radius: 10pt;
                    background-color: $secondary-color;
                    padding: 4pt 12pt;
                    width: 70pt;
                    font-size: 10pt;
                    margin-top: 23pt;

                    

                    .dot {
                        height: 5px;
                        width: 5px;
                        background-color: white;
                        border-radius: 50%;
                        display: inline-block;
                        margin: 2px 14px 2px 0px;
                    }
                    
                }

            }

            &-icon {
                height: 50pt;
                width: 50pt;
                background-color: white;
                border-radius: 50%;
                display: inline-block;
                color: $theme-color;
                margin: 24px 0;
                border: 1pt solid $theme-color;

                .img {
                    margin: 22px;
                    svg{
                        width: 17pt ;
                        height: 100%;
                    }
                }
            }

        }
        .display{
            display: block;
        }
        .hide{
            display: none;
        }

    }

    &__white {
        border: 2pt solid rgba(0, 134, 214, 0.2);
        border-radius: 15pt;
        // padding: 40pt 35pt;
        

        .center {
            display: flex;
            padding: 12pt 35pt 27pt;
        }

        .content {
            color: var(--gray);
            &-left {
                flex: 1;
                margin: 22px 0px;
            }

            &-icon {
                height: 50pt;
                width: 50pt;
                background-color: white;
                border-radius: 50%;
                display: inline-block;
                color: $theme-color;
                margin: 24px 0;
                border: 1pt solid $theme-color;

                .img {
                    margin: 22px;
                    svg{
                        width: 17pt ;
                        height: 100%;
                    }
                }
            }

        }

        .footer {
            background-color: $theme-color;
            color: white;
            padding: 12pt 14pt;
            border-bottom-left-radius: 15px;
            border-bottom-right-radius: 15px;
            margin-top: -41pt;
            margin-left: -2px;
            margin-right: -3px;

            strong {
                font-size: $body-text;
                margin: 0 10pt;
            }

            

            .subtopic {
                border: 1pt solid white;
                font-size: 10pt;
                padding: 3pt 11pt;
                border-radius: 4pt;
                margin: 0 5pt;
            }
        }
        
        .display{
            display: block;
        }
        .hide{
            display: none;
        }
    }   
    
}
