.checklist__picture{    
    &.one{
        background-image: url('../../assets/images/needexplorer-check-one.jpg');
    }
    &.two{
        background-image: url('../../assets/images/needexplorer-check-two.jpg');
    }
    &.three{
        background-image: url('../../assets/images/needexplorer-check-three.jpg');
    }
    &.four{
        background-image: url('../../assets/images/needexplorer-check-four.jpg');
    }
    &.selected{
        // box-shadow: inset 0px 0px 0px 5px white;
        box-shadow: inset 0px 0px 0px 5px white, -2px -1px 24px 7px rgba(0,0,0,0.15);
        -webkit-box-shadow: inset 0px 0px 0px 5px white, -2px -1px 24px 7px rgba(0,0,0,0.15);
        -moz-box-shadow: inset 0px 0px 0px 5px white, -2px -1px 24px 7px rgba(0,0,0,0.15);
    }
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    width: 90%;
    height: 75%;
    text-align: center;
    border-radius: 0.5em;
    position: relative;
    color: white;
    padding: 1em;
    
    .text{
        text-align: left;
        font-weight: bold;
        font-size: $body-text;
        position: absolute;
        bottom: 12%;
    }
    .checkboxContainer{
        position: absolute;
        top: 0;
        right: 0;
    }
    .checkbox{
        display: inline-block;
        padding-left: 30px;
        position: relative;
        cursor: pointer;
        user-select: none;
        input{
            display: none;
        }
        .checkmark{
            display: inline-block;
            width: 1em;
            height: 1em;
            background: rgba(255,255,255,0.3);
            position: absolute;
            left: 0;
            top: 0;
            border-radius: 1em;
        }
        input:checked + .checkmark {
            background-color: white;
            // border: 1px solid #1390e5;
        }
        input:indeterminate + .checkmark {
            background-color: white;
            // border: 1px solid #1390e5;
        }
        input:checked + .checkmark:after {
            content: "";
            position: absolute;
            height: 4px;
            width: 7px;
            border-left: 1px solid $theme-color;
            border-bottom: 1px solid $theme-color;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-45deg);
        }

        input:indeterminate + .checkmark:after {
            content: "";
            position: absolute;
            height: 0px;
            width: 11px;
            border-left: 1px solid $theme-color;
            border-bottom: 1px solid $theme-color;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(180deg);
        }
    }
}