.education-controls{
    width: 28.8em;
    background-color: rgb(47,59,74);
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    transition: all .5s ease-in-out;

    &--collapsed{
        width: 0em;

        .education-controls__control {
            transform: translateX(-100%);
        }
    }

    &__control{
        transition: all .5s ease-in-out;
        padding: 1.25em 3em 2em 1.25em;        
        width: 28.8em;

        &:not(:last-child) {
            border-bottom: 1px solid #6b7788eb;
        }

    
        > label {
            color: #fff;
            font-size: 1.2em;
            font-weight: bold;
            padding-bottom: 1em ;
            display: inline-block;

            display: flex;
            align-items: center;
            justify-content: space-between;
        }


        &-lens-slider{
            background-color: #5a6675;
            border-radius: 3px;

            > label {
                font-size: 1em;
                color: #fff;
                height: 3.4em;
                display: inline-flex;
                width: 100%;
                padding: 0 1em;
                align-items: center;
                border-bottom: 1px solid #303b4a;
            }

            > div {
                height: 3.4em;
                display: flex;
                align-items: center;
                justify-content: center;

                span {
                    color: #fff;
                    padding: 1em;
                }

                .slider{
                    height: 1px;
                    transform: translateY(-5px);

                    > div:last-child{
                        div{
                            width: 1em;
                            height: 1em;
                            background-color: #0386D6;
                        }
                    }
                }
            }
        }
    }
}

.temp-no-padding-bottom {
    padding-bottom: 0 !important;
}