.modal-summary{
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
    border-radius: 1em;
    background-color: #ffffff;
    width: 50em;
    height: 32em;
    z-index: 120;

    &__left{
        background-color: $theme-color;
        padding: 2em;
        width: 25%;
        height: -webkit-fill-available;
        border-radius: 1em 0em 0em 1em;

        &-lensType{
            font-size: 9pt;
            color: $theme-color;
            background-color: #ffffff;
            padding: 0.5em 1em;
            border-radius: 0.5em;
        }

        &-text{
            font-size: 12pt;
            color: #ffffff;
        }

        &-sub-text{
            color: white;
            font-size: 10pt;
            font-weight: 100;
        }
    }

    &__right{
        width: 70%;
        overflow-x: auto;
        margin: 0em 0em 0em 2em;

        .summary-lists{
            .summary-list{
                color: var(--gray);

                .answer{
                    font-size: 9pt;
                    border: 1px solid $theme-color;
                    padding: 0.5em 1em;
                    border-radius: 1em;
                    color: $theme-color;
                    margin: 0.2em;
                }
            }
        }
    }

    &__close-section{
        width: 7%;
        
    }
}

.modal-summary.show{
    display: flex;
}