.lens-summary {
    display: flex;
    color: $theme-color;
    height: 85vh;
    width: 100vw;
    position: absolute;
    bottom: 0;

    &__left {
        flex: 0;
        border: 1.5pt solid $theme-color;
        border-radius: 15pt;
        height: 89%;
        margin-top: 2%;
        width: 35%;
        position: sticky;

        &--header {
            margin: 15pt 12pt;
            font-size: $body-text;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 90%;
            margin-top: 5%;

            strong {
                text-transform: capitalize;
                margin: 0 7%;
            }

            .shp0 {
                fill: #ffffff !important;
            }

            .dot {
                height: 10pt;
                width: 10pt;
                background-color: transparent;
                border-radius: 50%;
                display: inline-block;
                margin: 2px 9px -1px 0px;
                border: 1px solid rgba(90, 102, 117, 0.1);
            }

            .icons {
                flex: 0 0 130px;
            }

            .vl {
                border-left: 1px solid var(--gray);
                height: 13pt;
                opacity: 10%;
                margin: 0 15pt;
                margin-top: 4pt;
            }

        }

        &--picture {
            margin: 0 25pt;
            height: 73%;
            position: relative;

            .demo {
                &--one {
                    position: absolute;
                    left: -4%;
                    opacity: 0.7;
                    height: 100%;
                }

                &--two {
                    position: absolute;
                    left: 6%;
                    opacity: 0.3;
                    height: 100%;
                }

                &--three {
                    margin-left: -15%;
                    height: 100%;
                }

            }
        }

        &--price {
            background-color: $theme-color;
            color: white;
            height: 16%;
            position: absolute;
            border-bottom-right-radius: 15pt;
            border-bottom-left-radius: 15pt;
            width: 100.5%;
            bottom: 0;
            font-size: 18pt;
            display: flex;
            justify-content: center;
            align-items: center;
            left: 0;

            &.display {
                display: none;
            }
        }
    }

    &__right {
        width: 60%;
        padding: 2em;

        &--header {
            width: 100%;
            display: flex;
            height: 2.7em;

            .options-btn {
                direction: rtl;
                display: flex;
                flex: 1;

                .vl {
                    border-left: 1px solid var(--gray);
                    height: 100%;
                    opacity: 10%;
                    margin: 0 3pt;
                }

                .export-icon {
                    right: 19%;
                    position: absolute;
                    width: 14pt;

                }
            }
        }

        &--offer {
            -webkit-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            -moz-box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            box-shadow: 0 10px 43px -2px rgba(0, 134, 214, 0.15);
            border-radius: 15pt;
            max-height: 265vh;
            margin-top: 5%;
            padding: 5% 2%;

            .content-offer {
                border: 1px solid $inactive-color;
                border-radius: 15pt;
                height: 100%;

                .summary-container {
                    margin: 1.5em 1em;
                    position: relative;
                    svg{
                            width: 3em;
                            height: 2.5em;
                    }
                    &__header {
                        display: flex;

                        h2 {
                            flex: 1;
                            color: #0692D0;
                            font-size: 1.3em;
                        }

                        .pair-holder {
                            background-color: $summary-box;
                            border-radius: 15pt;
                            padding: 1%;
                            width: 14%;
                            height: 10%;
                            font-weight: bold;
                            text-align: center;
                            margin: 1.2em 0;
                            font-size: 0.73em;

                            p {
                                background: linear-gradient(to right, $theme-color 0%, #5374CB 100%);
                                -webkit-background-clip: text;
                                -webkit-text-fill-color: transparent;
                                margin: 0.5em 0;
                            }
                        }
                    }

                    .holder {
                        display: flex;

                        &--one {
                            flex: 1;
                            position: relative;
                        }
                        
                    }

                    .summary-holder {
                        display: grid;
                        grid-template-areas: "title1 summary vertical";
                        grid-template-rows: 10em;
                        grid-template-columns: 33% 64% 3%;

                        &.two {
                            grid-template-rows: 20em;
                        }

                        &__label {
                            color: $light-gray;
                            display: grid;
                            grid-template-areas: "picture label";
                            width: 70%;
                            grid-area: title1;

                            &.two {
                                margin: 55% 0;

                                @media only screen and (min-width: 1366px) {
                                    margin: 40% 0;
                                }
                            }

                            svg {
                                grid-area: picture;
                                padding-right: 0.5em;
                                width: 3em;
                                height: 2.5em;
                            }

                            .capital {
                                text-transform: uppercase;
                                font-weight: bold;
                                grid-area: label;

                            }

                            .value {
                                grid-area: label;
                                padding-top: 1.5em;
                                font-size: 0.75em;
                            }
                        }

                        &__lens {
                            position: relative;
                            margin: 31% 0px;
                            height: 35%;

                            .horizontal-connection {
                                border-bottom: 0.5px solid $light-gray;
                                width: 32%;
                                position: absolute;
                                top: 45%;
                                right: 71%;
                            }

                            .horizontal-dotted {
                                border-bottom: 0.5px dashed $light-gray;
                                width: 20%;
                                position: absolute;
                                top: 45%;
                                right: 53%;
                            }


                            img {
                                width: 8em;
                            }

                        }

                        &__product {
                            grid-template-areas:
                                "line1 desc1 line2"
                                "line3 desc2 line4";
                            grid-template-columns: 12% 80% 8%;
                            grid-template-rows: repeat(2, 160px);
                            display: grid;
                            position: relative;

                            .two-lines {
                                position: relative;
                                grid-area: line1;
                                height: 10em;

                                @media only screen and (min-width: 1366px) {
                                    margin-right: 1.2em;
                                }

                                .vertical {
                                    border-left: 0.5px solid $light-gray;
                                    height: 100%;
                                    position: absolute;
                                    top: 8.5%;
                                    right: 74%;
                                }

                                .horizontal {
                                    border-top: 0.5px solid $light-gray;
                                    position: absolute;
                                    width: 57%;
                                    top: 58%;
                                    right: 74%;
                                }
                            }

                            .hl {
                                border-bottom: 0.5px solid $light-gray;
                                height: 10pt;
                                margin: 0 5pt;
                                grid-area: line1;

                                &.one {
                                    grid-area: line2;
                                    width: 100%;

                                    &.two {
                                        grid-area: line4;
                                    }
                                }

                                &.two {
                                    grid-area: line3;
                                }
                            }
                        }

                    }

                    .thickness-holder {
                        color: $light-gray;
                        display: flex;
                        font-weight: bold;

                        .value {
                            font-weight: normal;
                            background-color: $summary-box;
                            padding: 3% 4%;
                            margin-left: 4%;
                            font-size: 0.75em;
                        }
                    }

                }
            }


            .message-box {
                border: 1px solid #cce7f7;
                border-radius: 15pt;
                padding: 1.5em;
                margin: 1.5em 0;

                color h2 {
                    padding: 8px 10px;
                    font-size: 1.3em;
                }

                .notes {
                    border: none;
                    padding: 0;
                    background-attachment: local;
                    background-image:
                        -webkit-linear-gradient(right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 51%, rgba(255, 255, 255, 0) 100%),
                        -webkit-linear-gradient(bottom, rgba(128, 128, 128, 1) 0%, rgba(128, 128, 128, 0) 8%, rgba(128, 128, 128, 0) 100%);
                    line-height: 26px;
                    -webkit-background-size: 8px 26px;
                    -moz-background-size: 8px 26px;
                    -ms-background-size: 8px 26px;
                    -o-background-size: 8px 26px;
                    background-size: 8px 26px;
                }
            }

            .vertical-connection {
                border-left: 0.5px solid $light-gray;
                height: 30em;
                position: absolute;
                top: 2%;
                right: 1%;
                grid-area: vertical;
            }

            .essilor-logo-icon {
                float: right;
            }
        }
    }

    .modal-shadow {
        display: none;
        position: fixed;
        z-index: 999;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.4);

        
    }
    .modal-simple {
            height: 16em;
            width: 61%;
        }

    .modal__content {

        &--header {
            display: flex;
            position: relative;
            padding-top: 2em;

            .title {
                font-size: $header-text;
                color: $dark;
                margin-left: 10%;
            }

            .icon-btn {
                position: absolute;
                top: 0;
                right: 5%;
            }
        }

        &--btn {
            display: flex;
            color: $theme-color;
            height: 3em;
            justify-content: center;

            .summary-btn.export {
                border: 1pt solid $theme-color;
                padding: 1em;
            }

            .next-btn {
                flex-direction: unset;
            }

            img {
                width: 20pt;
                height: 100%;
                position: absolute;
                right: 6%;
                top: 0;
            }
        }
    }
}