.radio-btn {
    &.rect {
        padding: 1em;
        .radio {
            display: flex;
            padding: 15px 22px 15px 0pt;
            cursor: pointer;
            width: 100%;
            color: var(--gray);
        }

        .radio .product__text {
            flex: 1;
        }

        .radio .product__checkbox:after {
            background: $theme-color;
            width: 5pt;
            height: 5pt;
            position: absolute;
            top: 4px;
            left: 4.3px;
            transition: 300ms;
            opacity: 0;
            border-radius: 50%;

        }
    }

    &.square {
        height: 62%;
        width: 70%;
        display: grid;
        justify-content: center;
        padding: 1.4em 1em;

        .radio .product__checkbox {
            position: relative;
            line-height: 22px;
            padding: 10% 41%;
        }

        .radio .product__checkbox:after {
            background: $theme-color;
            width: 5pt;
            height: 5pt;
            position: absolute;
            top: 38%;
            left: 47.5%;
            transition: 300ms;
            opacity: 0;
            border-radius: 50%;
        }
        .radio .product__text {
            margin: 5pt 26pt;
            text-align: center;
        }


    }

    border: 1pt solid $border-light-blue;
    border-radius: 8pt;
    font-size: $body-text;
    
    margin: 1em;

    input {
        display: none;
    }

    .radio {
        display: block;
        padding: 15px 15px 15px 0pt;
        cursor: pointer;
        width: 100%;
        color: var(--gray);
    }



    .radio .product__checkbox {
        position: relative;
        line-height: 22px;
        float: right;
    }

    .radio .product__checkbox:before,
    .radio .product__checkbox:after {
        content: '';
    }


    .radio .product__checkbox:before {
        border: 1px solid $border-light-blue;
        width: 10pt;
        height: 10pt;
        // margin-right: 29px;
        display: inline-block;
        vertical-align: top;
        border-radius: 50%;
        position: relative;
    }

    .radio .product__text {
        margin-left: 20pt;
    }

    .radio input:checked~.product__checkbox:after {
        opacity: 1;
        background: white;
    }

    .radio input:checked~.product__checkbox:before {
        opacity: 1;
        border: 1px solid white
    }

    .radio input:checked~.product__text {
        color: white;
        opacity: 1;
        transition: 400ms;
    }

    .radio input:checked~.border.display {
        display: block;
        opacity: 1;
        transition: 400ms;
    }


}

.selected-answer {
    background-color: $theme-color;
    -webkit-box-shadow: 3px 4px 20px 3px rgba(0, 136, 214, 0.20);
    -moz-box-shadow: 3px 4px 20px 3px rgba(0, 136, 214, 0.20);
    box-shadow: 3px 4px 20px 3px rgba(0, 136, 214, 0.20);
}