.precision{
    margin-top: 1.3em;
    
    &__title{
        font-size: 0.8em;
        padding-left: 2em;
        color: $theme-color;
    }

    &__odOs{
        display: flex;

        &--container{
            display: flex;

            .type{
                width: 4em;
                background-color: rgb(204,231,247);
                border-radius: 0.5em 0em 0em 0.5em;
                text-align: center;
                color: var(--gray);
                font-size: 1.2em;
                padding: 0.6em 0em;
            }

            .value{
                width: 4em;
                background-color: #ffffff;
                border-radius: 0em 0.5em 0.5em 0em;
                border: 1px solid rgb(204,231,247);
                text-align: center;
                color: var(--gray);
                font-size: 1.2em;
                padding: 0.6em 2.8em;
            }
        }

        &--container:last-child{
            margin: 0em 1.5em;
        }
    }
}